body{
    margin: 0px;
  }
  
  .border{
    border: 2px solid red;
  }
  
  .wrapper{
    display: flex;
    flex-direction: row;
    border: 2px solid;
    margin-top: 5px;
  }
  
  .width-50{
    width: 50%;
    height: 50vh;
  }
  
  .btn{
    padding-top: 11px;
    padding-bottom: 10px;
    padding-left: 20px;
    padding-right: 20px;
    margin-right: 10px;
  }
.form{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 5px;
}
.label{
    display: inline-block;
    width: 150px;
}
.input{
    width: 200px;
    font-size: 15px;
}
.m-top{
    margin-top: 15px;
}
.registerform{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 30px;
    background: linear-gradient(138deg, #fdcd3b 38%, #ffed4b 70%);
    width: auto;
    margin: 15px 90px;
    height: auto;
    
}
.m-top-15{
    margin-top:15px;
}
.register_label{
    margin-right:50px;
}
.hobbies{
    display: flex;
    flex-direction: row;
    align-items: normal;
}
.checkboxes label {
    display: block;
    padding-right: 20px;
    padding-left: 268px;
    text-indent: 15px;
    padding-bottom: 15px;
  }
  .checkboxes input {
    vertical-align: middle;
  }
  .inputbox{
      height:25px;
      font-size: 15px;
  }
  .inputarea{
      height:50px;
      width:200px;
      font-size: 20px;
  }
 .err_msg{
     color: red;
     font-size: 20px;
 }
